<template>
  <div class="top">
    <ul>
      <li>
        <el-upload
          class="upload-demo"
          drag
          :on-change="handleChange"
          accept=".csv, .xlsx"
          multiple
          show-file-list="false"
          style="margin: 0 auto; margin-top: 60px"
        >
          <el-icon class="el-icon--upload"><UploadFilled /></el-icon>
          <div class="el-upload__text">
            Drop file here or <em>click to upload</em>
          </div>
        </el-upload>
      </li>
    </ul>
  </div>
  <div class="middle" style="margin-top: 60px">
    <ul style="margin: 0 auto">
      <li>
        <el-button type="danger" @click="forRedStone()">红石材料</el-button>
      </li>
      <li>
        <el-button type="primary" @click="forColor()">16色材料</el-button>
      </li>
      <li>
        <el-button type="primary" @click="forNotCollected()"
          >未收集材料</el-button
        >
      </li>
      <li>数量小于：</li>
      <li>
        <el-input-number
          v-model="this.inputNum"
          :min="1"
          controls-position="right"
          @change="handleChange"
        />
      </li>
      <li>组物品</li>
      <li><el-button type="info" @click="forSearch()">查询</el-button></li>
      <li><el-button @click="reset()">总材料</el-button></li>
      <li>
        <el-button type="success" @click="exportExcel()" :disabled="canTo"
          >导出</el-button
        >
      </li>
    </ul>
  </div>
  <div class="under" style="margin-top: 60px">
    <el-empty v-if="!flag" description="description" />
    <el-table
      v-loading="loading"
      v-if="flag"
      :data="this.nowData"
      style="width: 50%; margin-top: 260px; margin: 0 auto"
    >
      <el-table-column
        type="index"
        :index="indexMethod"
        style="width: 10%"
      ></el-table-column>

      <el-table-column
        prop="Item"
        label="材料"
        style="width: 40%"
      ></el-table-column>

      <el-table-column
        prop="Total"
        label="总量"
        style="width: 15%"
      ></el-table-column>

      <el-table-column
        prop="Box"
        label="分级数量"
        style="width: 15%"
      ></el-table-column>

      <el-table-column
        prop="Chest"
        label="箱装盒量"
        style="width: 15%"
      ></el-table-column>

      <el-table-column label="收集" style="width: 5%">
        <template #default="scope">
          <el-switch
            v-model="scope.row.Empty"
            @click="toCommond(scope.row)"
            active-value="true"
            inactive-value="false"
          />
        </template>
      </el-table-column>
    </el-table>
    <el-table id="outTable" :data="this.materialData" style="display: none">
      <el-table-column
        type="index"
        :index="indexMethod"
        style="width: 10%"
      ></el-table-column>

      <el-table-column
        prop="Item"
        label="Item"
        style="width: 40%"
      ></el-table-column>

      <el-table-column
        prop="Total"
        label="Total"
        style="width: 15%"
      ></el-table-column>

      <el-table-column
        prop="Box"
        label="Box"
        style="width: 15%"
      ></el-table-column>

      <el-table-column
        prop="Chest"
        label="Chest"
        style="width: 15%"
      ></el-table-column>

      <el-table-column
        prop="Empty"
        label="Empty"
        style="width: 15%"
      ></el-table-column>
    </el-table>
  </div>
  <h3 style="margin-top: 500px">By LUZaLID</h3>
</template>

<script>
import FileSaver from "file-saver";
export default {
  //名称 : Item
  //总量：Total
  //盒装量：Box
  //箱装盒量：Chest
  //是否收集：Empty

  data() {
    return {
      loading: true,
      canTo: true,
      flag: false,
      fileList: [],
      file: "",
      nowData: [],
      materialData: [],
      fileData: [],
      fileName: "csv files with a size less than 500kb",
      inputNum: 1,
      inputName: "",
      redStoneData: [
        "红石粉",
        "红石火把",
        "红石块",
        "红石中继器",
        "红石比较器",
        "活塞",
        "黏性活塞",
        "黏液块",
        "蜂蜜块",
        "侦测器",
        "漏斗",
        "发射器",
        "投掷器",
        "讲台",
        "标靶",
        "拉杆",
        "避雷针",
        "阳光探测器",
        "绊线钩",
        "陷阱箱",
        "TNT",
        "红石灯",
        "音符盒",
        "按钮",
        "压力板",
        "铁门",
        "铁活板门",
        "箱子",
      ],
      colorData: [
        "红色",
        "橙色",
        "黄色",
        "绿色",
        "青色",
        "蓝色",
        "紫色",
        "淡蓝色",
        "黄绿色",
        "粉红色",
        "白色",
        "棕色",
        "黑色",
        "灰色",
        "淡灰色",
        "品红色",
      ],
    };
  },

  methods: {
    clock() {
      this.loading = false;
    },

    handleChange(file, fileList) {
      this.fileList = [fileList[fileList.length - 1]]; // 只能上传一个Excel，重复上传会覆盖之前的
      this.file = file.raw;
      const _this = this;
      let reader = new FileReader();
      if (this.file.type == "text/csv") {
        reader.readAsText(this.file, "GB2312");
      } else {
        reader.readAsArrayBuffer(this.file);
      }
      console.log(this.file);
      reader.onload = function () {
        let buffer = reader.result;

        // let bytes = new Uint8Array(buffer);
        // console.log(bytes);
        // let length = bytes.byteLength;
        // let binary = "";
        // for (let i = 0; i < length; i++) {
        //   binary += String.fromCharCode(bytes[i]);
        // }
        let XLSX = require("xlsx");
        let wb = XLSX.read(buffer, {
          type: "string",
        });
        const outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]);
        _this.materialData = [...outdata];
        _this.fileName = file.name;
        _this.dataTranslate();
      };
      this.flag = true;
      this.canTo = false;
      this.loading = false;
    },

    exportExcel() {
      let XLSX = require("xlsx");
      //导出Excel
      /* generate workbook object from table */
      var wb = XLSX.utils.table_to_book(document.querySelector("#outTable"));
      /* get binary string as output */
      var wbout = XLSX.write(wb, {
        bookType: "xlsx",
        bookSST: true,
        type: "array",
      });
      try {
        FileSaver.saveAs(
          new Blob([wbout], { type: "application/octet-stream" }),
          "Diff.xlsx"
        );
      } catch (e) {
        if (typeof console !== "undefined") console.log(e, wbout);
      }
      this.$message({
        message: "操作成功",
        type: "success",
      });
      return wbout;
    },

    dataTranslate() {
      if (typeof this.materialData[0].Empty == "undefined") {
        for (var i = 0; i < this.materialData.length; i++) {
          this.materialData[i].Empty = false;
        }
      }
      //计算盒 组 个数
      for (var j = 0; j < this.materialData.length; j++) {
        var x = 0,
          y = 0,
          z = 0;
        if (
          this.materialData[j].Item.search("水桶") != -1 ||
          this.materialData[j].Item.search("岩浆桶") != -1 ||
          this.materialData[j].Item.search("潜影盒") != -1 ||
          this.materialData[j].Item.search("矿车") != -1 ||
          this.materialData[j].Item.search("蛋糕") != -1 ||
          this.materialData[j].Item.search("床") != -1
        ) {
          x = Math.floor(this.materialData[j].Total / 27);
          z = this.materialData[j].Total % 27;
        } else if (
          this.materialData[j].Item.search("旗帜") != -1 ||
          this.materialData[j].Item.search("告示牌") != -1
        ) {
          x = Math.floor(this.materialData[j].Total / (27 * 16));
          y = Math.floor((this.materialData[j].Total - x * 27 * 16) / 16);
          z = this.materialData[j].Total % 16;
        } else {
          x = Math.floor(this.materialData[j].Total / (27 * 64));
          y = Math.floor((this.materialData[j].Total - x * 27 * 64) / 64);
          z = this.materialData[j].Total % 64;
        }

        var box = "";
        if (x != 0) {
          box += x + "盒";
          var chest = parseFloat((x / 27).toFixed(2)) + "箱";
          this.materialData[j].Chest = chest;
        }
        if (y != 0) {
          if (box == "") {
            box += y + "组";
          } else {
            box += " + " + y + "组";
          }
        }
        if (z != 0) {
          if (box == "") {
            box += z + "个";
          } else {
            box += " + " + z + "个";
          }
        }
        this.materialData[j].Box = box;
      }
      this.nowData = JSON.parse(JSON.stringify(this.materialData));
      console.log(this.nowData);
    },

    toCommond(data) {
      console.log(data);
      for (var i = 0; i < this.materialData.length; i++) {
        if (this.materialData[i].Item == data.Item) {
          this.materialData[i].Empty = data.Empty;
        }
      }
    },

    update() {},

    reset() {
      this.loading = true;
      window.setTimeout(this.clock, 1000);
      this.nowData = [];
      this.nowData = JSON.parse(JSON.stringify(this.materialData));
    },

    forNotCollected() {
      this.loading = true;
      window.setTimeout(this.clock, 1000);
      this.nowData = [];
      for (var i = 0; i < this.materialData.length; i++) {
        if (this.materialData[i].Empty != "true") {
          this.nowData.push(this.materialData[i]);
        }
      }
    },

    forSearch() {
      this.loading = true;
      window.setTimeout(this.clock, 1000);
      console.log(this.inputNum);
      this.nowData = [];
      for (var j = 0; j < this.materialData.length; j++) {
        if (
          this.materialData[j].Item.search("水桶") != -1 ||
          this.materialData[j].Item.search("岩浆桶") != -1 ||
          this.materialData[j].Item.search("潜影盒") != -1 ||
          this.materialData[j].Item.search("矿车") != -1 ||
          this.materialData[j].Item.search("蛋糕") != -1 ||
          this.materialData[j].Item.search("床") != -1
        ) {
          if (this.materialData[j].Total < 1 * this.inputNum) {
            this.nowData.push(this.materialData[j]);
          }
        } else if (
          this.materialData[j].Item.search("旗帜") != -1 ||
          this.materialData[j].Item.search("告示牌") != -1
        ) {
          if (this.materialData[j].Total < 16 * this.inputNum) {
            this.nowData.push(this.materialData[j]);
          }
        } else {
          if (this.materialData[j].Total < 64 * this.inputNum) {
            this.nowData.push(this.materialData[j]);
          }
        }
      }
    },

    forRedStone() {
      this.loading = true;
      window.setTimeout(this.clock, 1000);
      var flag = 0;
      this.nowData = [];
      for (var i = 0; i < this.materialData.length; i++) {
        flag = 0;
        for (var j = 0; j < this.redStoneData.length; j++) {
          if (this.materialData[i].Item.search(this.redStoneData[j]) != -1) {
            flag = 1;
            break;
          }
        }
        if (flag == 1) {
          this.nowData.push(this.materialData[i]);
        }
      }
    },

    forColor() {
      this.loading = true;
      window.setTimeout(this.clock, 1000);
      var flag = 0;
      this.nowData = [];
      for (var i = 0; i < this.materialData.length; i++) {
        flag = 0;
        for (var j = 0; j < this.colorData.length; j++) {
          if (this.materialData[i].Item.search(this.colorData[j]) != -1) {
            flag = 1;
            break;
          }
        }
        if (flag == 1) {
          this.nowData.push(this.materialData[i]);
        }
      }
    },
  },
};
</script>

